/* mobile*/
.NavigationItem__NavigationItem__23bcu {
    margin: 10px 0;
    box-sizing: border-box;
    display: block;
    width: 100%;
    color: #c8c8c8;
    font-weight: 100;
}

.NavigationItem__NavigationItem__23bcu a {
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
    color: #c8c8c8;
}

.NavigationItem__NavigationItem__23bcu a:hover,
.NavigationItem__NavigationItem__23bcu a:active,
.NavigationItem__NavigationItem__23bcu a.NavigationItem__active__2zJdO {
    color: #4ec2dd;
    text-transform: uppercase;
    font-weight: 700;

}

/* dekstop*/
@media (min-width: 500px) {
    .NavigationItem__NavigationItem__23bcu {
        margin: 0;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
        color: #c8c8c8;
        font-weight: 100;

    
    }
    
    .NavigationItem__NavigationItem__23bcu a {
        color: #c8c8c8;
        height: 100%;
        padding: 0px 10px;
        border-bottom: 4px solid transparent;
        
    }
    
    .NavigationItem__NavigationItem__23bcu a:hover,
    .NavigationItem__NavigationItem__23bcu a:active,
    .NavigationItem__NavigationItem__23bcu a.NavigationItem__active__2zJdO {
        color: #4ec2dd;
        font-weight: 700;
    }

}

